.search-form {
  background-color: #006400;
  justify-content: center;
  margin-bottom: 20px;
  padding: 15px;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 15px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 240px;
  justify-content: center;
  align-items: baseline;
  margin: 0 auto;
  display: grid;
}

.photo-card {
  height: 100%;
  display: block;
  overflow: hidden;
  box-shadow: inset 1px -1px 5px rgba(154, 147, 140, .5), 1px 1px 5px #fff;
}

.gallery__image {
  height: 75%;
  width: 100%;
  object-fit: cover;
}

.info {
  height: 25%;
  align-items: center;
  padding: 0 7px;
  display: flex;
}

.info-item {
  margin-right: 10px;
}

.info-item:last-child {
  margin-right: 0;
}

.load-more {
  margin-top: 25px;
  margin-left: 100px;
}

